.login-background {

	min-height: 100vh;
	min-width: 100vw;
	background: url('../Img/fondo_login_escritorio.png');
	background-color: #ffd8cd;
	background-position-x: 50%; /* Center the image */
	background-repeat: no-repeat; /* Do not repeat the image */
	background-size: 100%; /* Resize the background image to cover the entire container */
}

.login-form{

	background: url('../Img/img_puntos.png');
	background-color: #fff;
	background-position-y: 10px;
	background-repeat: no-repeat; /* Do not repeat the image */
	background-position-x: 50%;
	background-size:95%; /* Resize the background image to cover the entire container */

	padding: 20px;
	border-radius: 15px;
	border: 2px solid black;
	-webkit-box-shadow: 12px 12px 0px -1px #e1251b; 
	box-shadow: 12px 12px 0px -1px #e1251b;

	overflow: auto;
	margin: auto;
	/* position: absolute;
	top: 0; left: 0; bottom: 0; right: 0; */
	/* top: 0; left: 0; bottom: 0; right: 0; */

}

